@import url('https://fonts.googleapis.com/css?family=Raleway:400,500');
* {
  margin: 0;
  padding: 0;
  list-style: none;
  vertical-align: baseline;
}

div {
  position: relative;
  z-index: 2;
}

.App {
  background-color: #333333;
  color: #333;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
  #quote-box {
    background-color: #fff;
    border-radius: 10px;
    color: #000;
    position: relative;
    display: table;
    width: 450px;
    padding: 40px 50px;
    #text {
      i {
        font-size: 1em;
        margin-right: 0.4em;
      }
      text-align: center;
      width: 450px;
      height: auto;
      clear: both;
      font-weight: 500;
      font-size: 1.75em;
    }
    #author {
      width: 450px;
      height: auto;
      clear: both;
      padding-top: 20px;
      font-size: 1em;
      text-align: end;
    }
    .buttons {
      width: 450px;
      margin: auto;
      display: block;
      .button {
        height: 38px;
        border: none;
        border-radius: 3px;
        color: #fff;
        background-color: #333;
        outline: none;
        font-size: 0.85em;
        padding: 8px 18px 6px 18px;
        margin-top: 30px;
        opacity: 1;
        cursor: pointer;
        &:hover {
          opacity: 0.9;
        }
        &#tweet-quote,
        &#tumblr-quote {
          float: left;
          padding: 0px;
          padding-top: 8px;
          text-align: center;
          font-size: 1.2em;
          margin-right: 5px;
          height: 30px;
          width: 40px;
        }
        &#new-quote {
          float: right;
        }
      }
    }
  }
}
